@import 'normalize.css';
@import "aos/dist/aos.css";

$xs: 570px;
$md: 768px;
$lg: 1148px;
$xl: 1280px;

$main-color: #303030;

@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  src: local("Montserrat"), url("../assets/fonts/Montserrat/Montserrat-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  src: local("Montserrat"), url("../assets/fonts/Montserrat/Montserrat-SemiBold.ttf") format("truetype");
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  letter-spacing: -1px;
  line-height: 1.4;
  color: $main-color;
  overflow-x: hidden;
  position: relative;
}

p {
  margin: 0;
  a {
    color: #F37D3D;
    &:hover {
      color: $main-color;
    }
  }
}

h1 {
  font-size: 40px;
  line-height: 48px;
  margin: 0;

  @media (max-width: $md) {
    font-size: 32px;
    line-height: 48px;
  }

  @media (max-width: $xs) {
    font-size: 24px;
    line-height: 32px;
  }

}

h2 {
  font-size: 32px;
  line-height: 48px;
  margin: 0;

  @media (max-width: $md) {
    font-size: 28px;
    line-height: 40px;
  }

  @media (max-width: $xs) {
    font-size: 18px;
    line-height: 24px;
  }
}

h3 {
  font-size: 28px;
  line-height: 40px;
  margin: 0;

  @media (max-width: $md) {
    font-size: 20px;
    line-height: 28px;
  }

  @media (max-width: $xs) {
    font-size: 16px;
    line-height: 24px;
  }
}

ul, ol {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

img {
  max-width: 100%;
  line-height: 0;
}

.logo {
  line-height: 0;
}

.button {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 20px 64px;
  background: linear-gradient(94.89deg, #FFA163 2.63%, #FFC441 95.35%);
  border: none;
  border-radius: 28px;
  line-height: 24px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  color: $main-color;
  @media (max-width: $md) {
    font-size: 16px;
  }
  @media (max-width: $xs) {
    font-size: 14px;
    padding: 12px;
  }
  &.large {
    padding: 20px 80px;
    @media (max-width: $xs) {
      padding: 12px 40px;
    }
  }
  &.outline {
    padding: 20px 40px;
    background: transparent;
    border: 2px solid $main-color;
    @media (max-width: $xs) {
      padding: 12px 12px;
    }
  }
}

.container {
  width: 100%;
  max-width: $xl;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  .logo {
    img {
      min-width: 163px;
      width: 163px;
    }
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 34px 0;
    column-gap: 56px;
    row-gap: 24px;
    flex-wrap: wrap;
    @media (max-width: $lg) {
      justify-content: space-between;
    }
    @media (max-width: $md) {
      justify-content: center;
    }

    nav {
      display: flex;
      flex: 1;
      justify-content: center;
      @media (max-width: $lg) {
        justify-content: flex-start;
      }
      @media (max-width: $md) {
        display: none;
      }
      ul {
        display: flex;
        gap: 56px;
        @media (max-width: $lg) {
          gap: 16px;
        }
        li {
          a {
            text-decoration: none;
            color: $main-color;
            white-space: nowrap;
          }
        }
      }
    }
  }
}

.main {
  .section {
    position: relative;
    padding: 56px 0;

    .wrapper {
      position: relative;
      z-index: 1;
    }

    &__title {
      text-align: center;
      margin-bottom: 24px;
    }
    &__subtitle {
      text-align: center;
      font-size: 20px;
      margin-bottom: 32px;
      @media (max-width: $xs) {
        font-size: 16px;
      }
    }
    &__buttons-group {
      display: flex;
      justify-content: center;
      gap: 24px;
      @media (max-width: $xs) {
        gap: 8px;
      }
    }

    &--one {
      background: url("../assets/images/bg1.svg") no-repeat center/100%, linear-gradient(180deg, #FFF2E7 40.75%, rgba(255, 242, 231, 0) 100%);
      padding: 324px 0 80px;
      @media (max-width: $md) {
        padding: 500px 0 80px;
      }
      @media (max-width: $md) {
        padding: 500px 0 20px;
      }
      &:after {
        content: "";
        width: 930px;
        height: 930px;
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(50%, -174px);
        background: #FFF4D5;
        border-radius: 50%;
        filter: blur(200px);
      }
      &:before {
        content: "";
        width: 766px;
        height: 766px;
        position: absolute;
        border-radius: 50%;
        filter: blur(250px);
        left: -262px;
        top: -286px;
        background: #FFE9D6;
      }
      .wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-end;
        position: relative;
        gap: 23px;
        @media (max-width: $md) {
          align-items: center;
          text-align: center;
        }
        &:after {
          content: "";
          width: 628px;
          height: 628px;
          position: absolute;
          left: 50%;
          top: 0;
          z-index: 1;
          pointer-events: none;
          transform: translate(calc(-50% + 400px), -200px);
          background-image: url("../assets/images/1.png");
          background-position: center;
          background-size: 100%;
          background-repeat: no-repeat;
          animation: move-bg-one 8s ease-in-out infinite;
          will-change: transform;
          @keyframes move-bg-one {
            0% {
              transform: translate(calc(-50% + 400px), -200px) scale(1) rotate(0)
            }

            50% {
              transform: translate(calc(-50% + 400px - 8px), calc(-200px - 6px)) scale(1.025) rotate(-1deg)
            }

            100% {
              transform: translate(calc(-50% + 400px), -200px) scale(1) rotate(0)
            }
          }
          @media (max-width: $lg) {
            left: 30%;
          }
          @media (max-width: $md) {
            left: 50%;
            transform: translate(-50%, -420px);
            animation: unset;
            width: 400px;
            height: 400px;

          }
        }

        & > * {
          z-index: 2;
          max-width: 628px;
        }
        p {
          font-size: 20px;
          @media (max-width: $xs) {
            font-size: 14px;
          }
        }
      }
    }
    &--two {
      @media (max-width: $xs) {
        padding: 20px 0;
      }
      .wrapper {
        padding: 200px 0 40px;
        display: flex;
        flex-direction: column;
        @media (max-width: $lg) {
          padding: 40px 0 0;
        }
        @media (max-width: $md) {
          padding: 0;
        }
        @media (max-width: $md) {
          gap: 24px;
        }
        @media (max-width: $xs) {
          gap: 16px;
        }
        & > div {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          position: relative;
          @media (max-width: $lg) {
            &:before, &:after {
              display: none;
            }
          }
          & > div {
            position: relative;
            max-width: 792px;
            border-radius: 40px;
            padding: 48px 124px 48px 40px;
            background: #F3D68773;
            backdrop-filter: blur(5px);
            z-index: 1;

            @media (max-width: $xs) {
              padding: 16px;
              border-radius: 20px;
            }
          }
          h2, h3 {
            color: #F37D3D;
            &:not(:last-child):not(:only-child) {
              margin-bottom: 16px;
            }
          }
          p {
            font-size: 20px;
            @media (max-width: $md) {
              font-size: 16px;
            }
            @media (max-width: $xs) {
              font-size: 14px;
            }
          }
          img {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(0, -50%);
            @media (max-width: $xs) {
              display: none;
            }
          }
          &:nth-child(1) {
            &:before {
              content: "";
              position: absolute;
              width: 368.61px;
              height: 358.84px;
              left: 620px;
              top: -168px;
              background: url('data:image/svg+xml,<svg width="340" height="348" viewBox="0 0 340 348" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M317.501 346.449L10.1972 222.984C-1.64239 218.228 -3.42381 202.205 7.08184 194.964L284.885 3.48482C294.947 -3.45069 308.755 2.99881 309.895 15.1665L339.396 330.11C340.513 342.035 328.614 350.914 317.501 346.449Z" fill="url(%23paint0_radial_11_61)"/><defs><radialGradient id="paint0_radial_11_61" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(150 195) rotate(-8.85849) scale(315.593 233.932)"><stop stop-color="%23FFD7C1"/><stop offset="0.910102" stop-color="%23FFF7F3"/></radialGradient></defs></svg>') no-repeat center/contain;
              animation: move-bg-two 8s ease-in-out infinite;
              @keyframes move-bg-two {
                0% {
                  transform: unset;
                }

                50% {
                  transform: translate(-8px, -6px) scale(1.025) rotate(-1deg)
                }

                100% {
                  transform: unset;
                }
              }
            }
            &:after {
              content: "";
              z-index: 0;
              position: absolute;
              width: 299.06px;
              height: 403.45px;
              left: -240px;
              bottom: -260px;
              background: url('data:image/svg+xml,<svg width="315" height="290" viewBox="0 0 315 290" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M304.996 16.3634L314.045 273.246C314.521 286.762 299.055 294.736 288.322 286.508L7.22478 71.0509C-3.96854 62.4714 0.607185 44.6575 14.5496 42.5344L286.597 1.10901C296.058 -0.331607 304.659 6.7996 304.996 16.3634Z" fill="url(%23paint0_radial_11_58)"/><defs><radialGradient id="paint0_radial_11_58" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(169.651 119.153) rotate(-137.869) scale(256.07 189.812)"><stop stop-color="%23FFD7C1"/><stop offset="1" stop-color="%23FFF7F3"/></radialGradient></defs></svg>') no-repeat center/contain;
            }
          }
          &:nth-child(2) {
            align-items: flex-end;
            margin-top: -55px;
            @media (max-width: $lg) {
              margin-top: -20px;
            }
            @media (max-width: $md) {
              margin-top: 0;
            }
            &:before {
              content: "";
              position: absolute;
              width: 160.36px;
              height: 156.11px;
              right: 700px;
              bottom: -110px;
              background: url('data:image/svg+xml,<svg width="146" height="134" viewBox="0 0 146 134" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M50.1786 124.438L1.79461 22.9646C-3.69696 11.4474 5.84125 -1.54937 18.4752 0.235618L131.879 16.2578C143.98 17.9675 149.774 32.0635 142.374 41.7891L77.354 127.24C70.1017 136.771 55.3333 135.248 50.1786 124.438Z" fill="url(%23paint0_radial_11_67)"/><defs><radialGradient id="paint0_radial_11_67" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(127.96 38.7456) rotate(133.667) scale(131.848 97.7323)"><stop stop-color="%23FFD7C1"/><stop offset="1" stop-color="%23FFF7F3"/></radialGradient></defs></svg>') no-repeat center/contain;
              animation: move-bg-three 8s ease-in-out infinite;
              @keyframes move-bg-three {
                0% {
                  transform: unset;
                }

                50% {
                  transform: translate(-8px, -6px) scale(1.025) rotate(-1deg)
                }

                100% {
                  transform: unset;
                }
              }
            }
            &:after {
              content: "";
              z-index: 0;
              position: absolute;
              width: 160.36px;
              height: 156.11px;
              top: -30px;
              right: -102px;
              background: url('data:image/svg+xml,<svg width="136" height="145" viewBox="0 0 136 145" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M131.151 57.1876L54.3011 139.236C45.5787 148.548 29.9991 144.405 27.0553 131.989L0.631286 20.5493C-2.18828 8.65808 8.82519 -1.87635 20.5793 1.4689L123.854 30.861C135.373 34.1394 139.339 48.4466 131.151 57.1876Z" fill="url(%23paint0_radial_11_62)"/><defs><radialGradient id="paint0_radial_11_62" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(23 16) rotate(22.2856) scale(131.848 97.7323)"><stop stop-color="%23FFD7C1"/><stop offset="1" stop-color="%23FFF7F3"/></radialGradient></defs></svg>') no-repeat center/contain;
            }
            & > div {
              background: #FFC28D73;
              backdrop-filter: blur(25px);
              h3:not(:last-child):not(:only-child) {
                margin-bottom: 0;
              }
              img {
                right: 8px;
                top: auto;
                bottom: 18px;
                transform: unset;
                @media (max-width: $xs) {
                  display: none;
                }
              }
            }
          }
        }
      }
    }

    &--three {
      @media (max-width: $xs) {
        padding: 20px 0;
      }
    }

    &--four {
      padding-bottom: 80px;
      @media (max-width: $xs) {
        padding: 20px 0 40px 0;
      }
      &:before {
        content: "";
        position: absolute;
        width: 766px;
        height: 766px;
        left: 0;
        top: 50%;
        transform: translate(-30%, -50%);
        background: #FFE9D6;
        filter: blur(250px);
      }
      &:after {
        content: "";
        position: absolute;
        width: 930px;
        height: 930px;
        right: 0;
        top: 100px;
        transform: translateX(40%);
        background: #FFF4D5;
        filter: blur(200px);
      }

    }
    &--five {
      background: #231F20;
      border-top-left-radius: 80px;
      border-top-right-radius: 80px;
      color: #FFFFFF;
      overflow: hidden;
      @media (max-width: $md) {
        border-top-left-radius: 40px;
        border-top-right-radius: 40px;
      }
      @media (max-width: $xs) {
        padding: 24px 0 64px;
      }
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 148px;
        left: 50%;
        transform: translateX(-50%);
        bottom: -148px;
        background: #93C25B;
        filter: blur(100px);

      }
    }

    &--six {
      padding: 0;
      .section__title {
        margin-bottom: 22px;
      }
      .wrapper {
        padding: 64px 15px 56px;
        background: #FFEEDF;
        border-bottom-left-radius: 40px;
        border-bottom-right-radius: 40px;
        @media (max-width: $md) {
          margin-left: -15px;
          width: calc(100% + 30px);
          padding: 24px 15px;
        }
      }
    }
    &--seven {
      padding-bottom: 24px;
      @media (max-width: $xs) {
        padding: 40px 0 24px;
      }
    }

  }
}

.footer {
  padding: 72px 0 56px 0;
  position: relative;
  overflow: hidden;
  @media (max-width: $xs) {
    padding-top: 16px;
  }
  .wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
  }

  &:before {
    content: "";
    position: absolute;
    width: 766px;
    height: 766px;
    left: 0;
    bottom: 0;
    transform: translate(-40%, 75%);
    background: #FFE9D6;
    border-radius: 50%;
    filter: blur(250px);

  }
  &:after {
    content: "";
    position: absolute;
    width: 930px;
    height: 930px;
    right: 0;
    bottom: 0;
    transform: translate(50%, 75%);
    background: #FFF4D5;
    border-radius: 50%;
    filter: blur(200px);
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #DDDDDD;
    border-bottom: 1px solid #DDDDDD;
    padding: 32px 0;
    margin-bottom: 24px;
    gap: 24px;
    @media (max-width: $xs) {
      flex-wrap: wrap;
      font-size: 14px;
    }
    .logo {
      @media (max-width: $xs) {
        max-width: 93px;
      }
    }
    ul {
      display: flex;
      column-gap: 40px;
      row-gap: 8px;
      font-size: 20px;
      flex-wrap: wrap;
      @media (max-width: $md) {
        font-size: 16px;
      }
      @media (max-width: $xs) {
        min-width: 100%;
        justify-content: space-between;
      }
      li {
        padding: 0 24px;
        @media (max-width: $xs) {
          padding: 0;
          white-space: nowrap;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #6D6D6D;
    font-size: 12px;
    gap: 16px;
    @media (max-width: $xs) {
      align-items: flex-start;
    }
    ul {
      display: flex;
      @media (max-width: $xs) {
        flex-direction: column;
        gap: 8px;
      }
      li {
        padding: 0 24px;
        @media (max-width: $xs) {
          padding: 0;
        }
        &:not(:last-child) {
          border-right: 1px solid #DDDDDD;
          @media (max-width: $xs) {
            border-right: none;
          }
        }
        a {
          color: #6D6D6D;
          text-decoration: none;
        }
      }
    }
  }
}

.use-examples {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  @media (max-width: $md) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: $xs) {
    gap: 8px;
  }
  &__item {
    text-align: center;
    @media (max-width: $xs) {
      font-size: 10px;
      letter-spacing: 0;
    }
    &__image {
      margin-bottom: 32px;
      img {
        width: 196px;
      }
    }

  }
}

.compare-table {
  display: flex;
  flex-direction: column;
  position: relative;

  &__mobile-header {
    display: none;
    @media (max-width: $md) {
      display: flex;
      font-size: 10px;
      justify-content: space-between;
      text-align: center;
      gap: 12px;
    }
    & > span {
      flex: 1;
      letter-spacing: 0;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
  &__row {
    padding: 24px 16px;
    display: flex;
    border-radius: 16px;
    z-index: 1;
    position: relative;
    gap: 12px;
    @media (max-width: $md) {
      flex-wrap: wrap;
    }
    &--header {
      font-weight: 600;
      @media (max-width: $md) {
        display: none;
      }
    }
    &:not(&--header) {
      &:nth-child(even) {
        background-color: #FFFFFF;
      }
    }
  }
  &__cell {
    flex: 1;
    display: flex;
    @media (max-width: $xs) {
      font-size: 12px;
      letter-spacing: 0;
    }
    &:nth-child(1) {
      min-width: 465px;
      @media (max-width: $lg) {
        min-width: 300px;
      }
      @media (max-width: $md) {
        min-width: 100%;
      }
    }
    &:not(:first-child) {
      justify-content: center;
      text-align: center;
    }
    @media (max-width: $md) {
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  }
  &:after {
    content: "";
    z-index: 0;
    position: absolute;
    width: 160.36px;
    height: 156.11px;
    right: -160px;
    top: 0;
    background: url('data:image/svg+xml,<svg width="136" height="145" viewBox="0 0 136 145" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M131.151 57.1876L54.3011 139.236C45.5787 148.548 29.9991 144.405 27.0553 131.989L0.631286 20.5493C-2.18828 8.65808 8.82519 -1.87635 20.5793 1.4689L123.854 30.861C135.373 34.1394 139.339 48.4466 131.151 57.1876Z" fill="url(%23paint0_radial_59_158)"/><defs><radialGradient id="paint0_radial_59_158" cx="0" cy="0" r="1" gradientUnits="userSpaceOnUse" gradientTransform="translate(23 16) rotate(22.2856) scale(131.848 97.7323)"><stop stop-color="%23FFD7C1"/><stop offset="1" stop-color="%23FFF7F3"/></radialGradient></defs></svg>') no-repeat center/contain;
    @media (max-width: $md) {
      display: none;
    }
  }
  &:before {
    content: "";
    position: absolute;
    width: 299.06px;
    height: 403.45px;
    left: -250px;
    bottom: -60px;
