.advantages {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  @media (max-width: $xs) {
    gap: 12px;
  }
  &__item {
    padding: 16px 24px 16px 24px;
    position: relative;
    gap: 12px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    border-radius: 50px;
    font-size: 20px;
    transition: .1s transform;
    will-change: transform;
    @media (max-width: $md) {
      font-size: 16px;
    }
    @media (max-width: $xs) {
      padding: 12px 8px;
      font-size: 14px;
      border-radius: 24px;
    }
    &:not(:first-child) {
      box-shadow: 0px 0px 8px 0px #3C561B;
    }

    &:first-child {
      width: 100%;
      max-width: 963px;
      text-align: center;
      background: linear-gradient(136.41deg, #A2E454 2.68%, #487018 97.51%);
      color: $main-color;
      &:before {
        background: $main-color;
      }
      &:after {
        display: none;
      }
    }
    &:before {
      content: "";
      width: 8px;
      height: 8px;
      min-width: 8px;
      border-radius: 50%;
      background: #93C25B;
      @media (max-width: $lg) {
        display: none;
      }

    }
    &:after {
      content: "";
      position: absolute;
      inset: 0;
      pointer-events: none;
      border-radius: 50px;
      border: 2px solid transparent;
      background: linear-gradient(136.41deg,#A2E454 2.68%,#487018 97.51%) border-box;
      mask: linear-gradient(#000000 0 0) padding-box, linear-gradient(#000000 0 0);
      mask-composite: exclude;
      @media (max-width: $xs) {
        border-radius: 24px;
      }
    }
    &:hover {
      transform: scale(1.025);
    }
  }
}

.steps {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  @media (max-width: $md) {
    grid-template-columns: repeat(2, 1fr);
  }
  &__item {
    flex: 1;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    font-size: 20px;
    @media (max-width: $md) {
      font-size: 16px;
    }
    @media (max-width: $xs) {
      font-size: 12px;
      letter-spacing: 0;
    }
    &__image {
      margin-bottom: 16px;
      width: 140px;
      height: 140px;
      position: relative;
      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        object-fit: contain;
      }
    }
  }
}

.tech-info {
  display: flex;
  border: 1px solid #DDDDDD;
  border-radius: 24px;
  min-height: 188px;
  background: #FFFFFF url("../assets/images/12.png") no-repeat right top/150px;
  margin-bottom: 80px;
  @media (max-width: $xs) {
    flex-direction: column;
    margin-bottom: 40px;
  }
  &__item {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 24px 40px;
    gap: 24px;
    @media (max-width: $xs) {
      padding: 16px;
    }
    img {
      width: 93px;
    }

    &:not(:last-child) {
      border-right: 1px solid #DDDDDD;
      @media (max-width: $xs) {
        border-right: none;
        border-bottom: 1px solid #DDDDDD;
      }
    }
    & > div {
      &:only-child {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }
      &:not(:only-child):last-child {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }
    }
  }
}